import React, { useContext } from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import firebase from '../config/firebase';
import AppContext from '../store/AppContext';

export default function Header() {
    const [isLoggedIn, user] = useContext(AppContext);

    const history = useHistory();


    function logout() {
        console.log("loguout called");
        firebase.auth().signOut().then((res) => {
            console.log("logged out s");
            history.replace('/login');
        }).catch((e) => {
            console.log(e.response.data);
            console.log("Failed to logout");
        });
    }

    return (
        <nav className="py-5 bg-gray-900 text-white flex justify-between">
            <ul className="flex justify-between px-10">

                <li className="mr-5">
                    <NavLink to="/" exact activeClassName="underline text-blue-200">Home</NavLink>
                </li>

                <li>
                    <NavLink to="/gallery" activeClassName="underline text-blue-200">Gallery</NavLink>
                </li>
                <li>
                    <NavLink to="/tensorflow" exact className="ml-5" activeClassName="underline text-blue-200" >Tensorflow</NavLink>
                </li>

            </ul>

            <ul className="flex justify-between px-10">
                <li>
                    {
                        isLoggedIn ? (<button onClick={logout}>Logout</button>) : (<NavLink to="/login" activeClassName="underline text-blue-200">Login</NavLink>)
                    }
                </li>
                {
                    !isLoggedIn && (
                        <li className="ml-5">
                            <NavLink to="/signup" activeClassName="underline text-blue-200">SignUp</NavLink>

                        </li>)
                }

            </ul>
        </nav>
    )
}
