import React, { useState } from 'react'
import { Redirect, useHistory } from 'react-router-dom';
import firebase from '../config/firebase';

export default function Login() {
    const [isLoading, setisLoading] = useState(false);
    const [error, setError] = useState("");
    const [form, setForm] = useState({email:"", password:""});
    // const [isLoggedIn, setIsLoggedIn] = useState(false);
    const history = useHistory();

    function handleForm(e) {
        if (isLoading) return;
        setisLoading(true);
        e.preventDefault();

        firebase.auth().signInWithEmailAndPassword(form.email, form.password)
            .then((res) => {
                // setIsLoggedIn(true);
                history.replace("/");
                setError("");
                setisLoading(false);
            })
            .catch((e) => {
                setError(e.message);
                setisLoading(false);
            });
    }

    function handleInput(e) {
        setForm({...form,[e.target.name]: e.target.value});
    }

    // if (isLoggedIn) return <Redirect to="/" />;

    return (
        <div className="flex h-screen bg-gray-200">
            <div className="m-auto w-1/3  flex flex-wrap 
            justify-center shadow-lg rounded-lg bg-gradient-to-br 
            from-indigo-900 to-indigo-700">

                <form className="m-5 w-10/12 " onSubmit={handleForm}>

                    {error!="" && <p className="text-white">{error}</p>}

                    <h1 className="w-full text-4xl text-white tracking-widest text-center my-6">
                        Login
                    </h1>
                    <div className="w-full my-6">
                        <input
                            type="email"
                            className="p-2 rounded shadow w-full"
                            placeholder="Email or Username"
                            name="email"
                            value={form.email}
                            onChange={handleInput}
                        />
                    </div>

                    <div className="w-full my-6">
                        <input
                            type="password"
                            className="p-2 rounded shadow w-full"
                            name="password"
                            placeholder="Password"
                            value={form.password}
                            onChange={handleInput}
                        />
                    </div>

                    <div className="w-full my-10">
                        <button
                            type="submit"
                            className="p-2 rounded shadow w-full bg-yellow-400 text-black
                            bg-gradient-to-tr from-yellow-600 to-yellow-400 "
                        >
                            {
                                isLoading ? <i className="fas fa-circle-notch fa-spin" ></i>
                                    : "Login"
                            }

                        </button>
                    </div>
                </form>
            </div>
        </div>
    )
}
